.category-section {
    width: 30%;
}

#category-section-nav {
    display: flex;
    justify-content: space-between;
    font-family: "Helvetica";
    color: #453F35;
    font-size: 12px;
    line-height: 0.5;
}

#category-section-nav > div:not(:last-child) {
    margin-right: 5px;
}

#category-section-nav > .category-section-nav-target {
    text-decoration: underline;
}

.category-section-button {
    width: 100%;
    font-family: "Helvetica";
    margin-top: 10px;
}

.category-section-button-text {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

@media screen and (max-width: 410px) {
    #category-section-nav {
        flex-direction: column;
        line-height: initial;
    }

    #category-section-nav > div:first-child {
        line-height: 0.5;
    }
}


/*Style for button*/
input:disabled,
button:disabled,
.button-disabled {
    padding: 0.5rem;
    font-size: 1em;
    border: none;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0;
    background-color: #dcd8c0;
    color: inherit;
    letter-spacing: inherit;
    font-weight: inherit;
    color: #bab5a1;
    cursor: not-allowed;
}

button:not(:disabled),
.button {
    padding: 0.5rem;
    font-size: 1em;
    border: none;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0;
    background-color: #bab5a1;
    color: inherit;
    letter-spacing: inherit;
    font-weight: inherit;
    cursor: pointer;
    -webkit-transition-duration: 0.2s;
    -moz-transition-duration: 0.2s;
    -o-transition-duration: 0.2s;
    -ms-transition-duration: 0.2s;
    transition-duration: 0.2s;
    -webkit-transition-property: color, background-color, box-shadow;
    -moz-transition-property: color, background-color, box-shadow;
    -o-transition-property: color, background-color, box-shadow;
    -ms-transition-property: color, background-color, box-shadow;
    transition-property: color, background-color, box-shadow;
    position: relative;
    z-index: 1;
}

button:not(:disabled):hover,
.button:hover {
    -webkit-box-shadow: 0.2em 0.2em 0.1em 0 #bab5a1;
    box-shadow: 0.2em 0.2em 0.1em 0 #bab5a1;
}

button:not(:disabled):before,
.button:before {
    content: '';
    -webkit-transition: all 0.2s;
    -moz-transition: all 0.2s;
    -o-transition: all 0.2s;
    -ms-transition: all 0.2s;
    transition: all 0.2s;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

button:not(:disabled):after,
.button:after {
    content: '';
    -webkit-transition: all 0.2s;
    -moz-transition: all 0.2s;
    -o-transition: all 0.2s;
    -ms-transition: all 0.2s;
    transition: all 0.2s;
    -webkit-transition-timing-function: ease-out;
    -moz-transition-timing-function: ease-out;
    -o-transition-timing-function: ease-out;
    -ms-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 0;
    background-color: #454138;
    z-index: -1;
}

button:not(:disabled):hover,
.button:hover {
    background-color: transparent;
    color: #dcd8c0;
}

button:not(:disabled):hover:before,
.button:hover:before {
    top: -0.2rem;
    bottom: -0.2rem;
    border: solid #454138;
    border-width: 0.1rem 0;
}

button:not(:disabled):hover:after,
.button:hover:after {
    width: 100%;
}

button:not(:disabled):active,
.button:active {
    color: #454138;
}

button:not(:disabled):active:after,
.button:active:after {
    background-color: #dcd8c0;
}

::-webkit-input-placeholder,
::-moz-placeholder,
::-ms-input-placeholder {
    opacity: 1;
    -ms-filter: none;
    filter: none;
    color: #bab5a1;
    font-weight: lighter;
}